import React, { Fragment } from "react";
import {
    SectionProjects, ContainerProjects,
    TitleProjects, Description, ContainerDescription,
    IMGContainer, Title, Img2
} from "../styles/ProjectsPageStyles"
import PokeGame from "../img/PokeGame.svg"
import {useWidth} from "./useWidth";
import {fadeInContainerWithStagger,fadeInDown} from "../styles/Animations.js"
import {useInViewport} from "./useInViewport.jsx"
import SearchLol from "../img/LolSearch.png"
const ProjectsPage = React.forwardRef((props,ref) => {
    const inViewport = useInViewport(ref,0.2)
    const widthPC = useWidth();
    return (
        <Fragment>
                <SectionProjects ref={ref}>
                {inViewport &&
                <Fragment>
                    <ContainerProjects
                    variants={fadeInContainerWithStagger}
                    initial="hidden"
                    animate="visible" >
                        { !widthPC &&
                         <TitleProjects
                          variants={fadeInDown}>
                            Projects
                         </TitleProjects>
                        }<Description>
                                <ContainerDescription
                                initial={{opacity:0, x:"-100px"}}
                                animate={{opacity:1,x:"0"}}
                                transition={{duration:1,delay:1 ,type: "tween", ease: "easeIn"}}>
                                    <Title>
                                        <span>0.1</span>
                                        <h3>Memory Game</h3>
                                    </Title>
                                    <p>
                                       Application that consumes the pokemon api developed in react, and designed in adobe XD
                            </p>
                            <h5>Tap in the image to view more</h5>
                                </ContainerDescription >
                                <IMGContainer
                                variants={fadeInDown}
                                whileTap={{scale:0.9}}
                                href="https://jarero321.github.io/pokegame/" >
                                    <img src={PokeGame}></img>
                                </IMGContainer>
                            </Description>
                </ContainerProjects>
                 <ContainerProjects
                 variants={fadeInContainerWithStagger}
                 initial="hidden"
                 animate="visible" >
                 <Description>
                             <ContainerDescription
                             initial={{opacity:0, x:"-100px"}}
                             animate={{opacity:1,x:"0"}}
                             transition={{duration:1,delay:1 ,type: "tween", ease: "easeIn"}}>
                                 <Title>
                                     <span>0.2</span>
                                     <h3>Champion Selector</h3>
                                 </Title>
                                 <p>
                                    Application that consumes the league of legends api developed in react with dynamics routers and form validation
                         </p>
                         <h5>Tap in the image to view more</h5>
                             </ContainerDescription >
                             <IMGContainer
                             variants={fadeInDown}
                             whileTap={{scale:0.9}}
                             href="https://jarero321.github.io/SearchLol/" >
                                 <Img2 src={SearchLol}></Img2>
                             </IMGContainer>
                         </Description>
             </ContainerProjects>
             </Fragment>
                
}
                </SectionProjects>
        </Fragment>
    )

})
export default ProjectsPage